import {Toast, Modal} from 'antd-mobile'

window.alert = str => Toast.info(str, 2);
window.confirm = function (str, onOK, onCancel) {
    Modal.alert('', str, [
        {
            text: '',
            onPress: () => {
                onCancel && onCancel()
            }, style: 'default'
        },
        {
            text: '',
            onPress: () => {
                onOK && onOK();
            }, style: {fontWeight: 'bold'}
        },
    ])
};


